* {
	// margin: 0;
	// padding: 0;
	// box-sizing: border-box;
	--locked-color: #5fadbf;
	--unlocked-color: #ff5153;
}

.lock {
	width: 24px;
	height: 21px;
	border: 3px solid var(--locked-color);
	border-radius: 5px;
	position: relative;
	cursor: pointer;
	-webkit-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
	background-color: var(--locked-color);

	&:after {
		content: "";
		display: block;
		background-color: white;
		width: 3px;
		height: 7px;
		position: absolute;
		top: 50%;
		left: 50%;
		margin: -3.5px 0 0 -2px;
		-webkit-transition: all 0.1s ease-in-out;
		transition: all 0.1s ease-in-out;
		border-radius: 5px;
	}

	&:before {
		content: "";
		display: block;
		width: 10px;
		height: 10px;
		bottom: 100%;
		position: absolute;
		left: 50%;
		margin-left: -8px;
		border: 3px solid var(--locked-color);
		border-top-right-radius: 50%;
		border-top-left-radius: 50%;
		border-bottom: 0;
		-webkit-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
	}
}



/* Locked Hover */
.lock:hover:before {
	height: 12px;
}
/* Unlocked */
.unlocked {
	transform: rotate(10deg);
}
.unlocked:before {
	bottom: 130%;
	left: 31%;
	margin-left: -11.5px;
	transform: rotate(-45deg);
}
.unlocked,
.unlocked:before {
	border-color: var(--unlocked-color);
}
.unlocked:after {
	background: var(--unlocked-color);
}
/* Unlocked Hover */
.unlocked:hover {
	transform: rotate(3deg);
}
.unlocked:hover:before {
	height: 10px;
	left: 40%;
	bottom: 124%;
	transform: rotate(-30deg);
}