@import "../../css/_variables";

.app {
	background: #b7f4ff;
	background: linear-gradient(180deg, #b7f4ff 0%, #fff6b8 100%);
	background-repeat: no-repeat;
	min-height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-transform: uppercase;
}

.input {
	flex-grow: 1;
	transition: transform 3s;
	transform: scale(1);
}

.mr-2 {
	margin-right: 10px;
}

.icon {
	width: 60px;
    background: yellow;
    display: flex;
    justify-content: center;
}

