.wrapper_nav {
	position: absolute;
	bottom: $spacing;
	right: -40px;

	.button {
		padding: $spacing / 2 $spacing;
		padding-right: 50px;
	}

	&.wrapper_nav--left {
		right: auto;
		left: -40px;
		justify-content: flex-end;

		.button {
			padding-left: 60px;
			padding-right: $spacing;
		}
	}
}








