.wrapper {
	width: $prism-length;
	height: $prism-height;
	margin: 60px auto;
	perspective: 600px;
	text-align: left;
	// font-size: 30px;
}

.rec-prism {
	width: 100%;
	height: 100%;
	position: relative;
	transform-style: preserve-3d;
	transform: translateZ(-100px);
	transition: transform 0.5s ease-in;
}

